@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

p {
  color: rgba(255, 255, 255, 0.75);
  font-family: "Lexend Deca", sans-serif;
  font-size: 15px;
  line-height: 1.8rem;
}

h2 {
  color: #f2f2f2;
  text-transform: uppercase;
  font-family: "Big Shoulders Display", cursive;
  font-weight: 700;
  font-size: 35px;
}

.learn-sec {
  background-color: #f2f2f2;
  font-size: 15px;
  font-family: "Lexend Deca", sans-serif;
  padding-left: 15%;
  padding-right: 15%;
  padding-top: 15px;
  padding-bottom: 15px;
  margin: auto 0 0 0;
  width: max-content;
  border-radius: 25px;
}
.learn-sec:hover {
  cursor: pointer;
  background-color: inherit;
  outline: solid 2px rgba(255, 255, 255, 0.75);
}

#sedan-learn {
  color: #e38826;
}
#sedan-learn:hover {
  color: rgba(255, 255, 255, 0.75);
}

#suv-learn {
  color: #006970;
}
#suv-learn:hover {
  color: rgba(255, 255, 255, 0.75);
}

#luxury-learn {
  color: #004241;
}
#luxury-learn:hover {
  color: rgba(255, 255, 255, 0.75);
}

.footer-content {
  padding-top: 50px;
  font-size: 13px;
  font-family: "Lexend Deca", sans-serif;
  text-align: center;
}
.footer-content a {
  text-decoration: none;
  font-family: "Big Shoulders Display", cursive;
}

/*
============================
STYLING STARTS HERE 
============================
*/
body {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main {
  display: grid;
  grid-template-columns: 18rem 18rem 18rem;
  grid-template-rows: 35rem;
}

/* -------------------------------------------------------------------------- */
/*                            ICONS ADDED FROM HERE                           */
/* -------------------------------------------------------------------------- */
/* ------------------------------- SEDAN ICON ------------------------------- */
.sedan-icon {
  height: 48px;
  width: 90px;
  background: url("../images/icon-sedans.svg") no-repeat top left;
  background-size: contain;
}

/* -------------------------------- SUV ICON ------------------------------- */
.suv-icon {
  height: 48px;
  width: 90px;
  background: url("../images/icon-suvs.svg") no-repeat top left;
  background-size: contain;
}

/* ------------------------------ LUXURY ICON ------------------------------ */
.luxury-icon {
  height: 48px;
  width: 90px;
  background: url("../images/icon-luxury.svg") no-repeat top left;
  background-size: contain;
}

/* -------------------------------------------------------------------------- */
/*                              DIV SECTIONS HERE                             */
/* -------------------------------------------------------------------------- */
/* ----------------------------- SEDAN SECTION ----------------------------- */
.sedan-sec {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
  background-color: #e38826;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

/* ------------------------------- SUV SECTION ------------------------------ */
.suv-sec {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
  background-color: #006970;
}

/* ----------------------------- LUXURY SECTION ---------------------------- */
.luxury-sec {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 3rem;
  background-color: #004241;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

/* -------------------------------------------------------------------------- */
/*                                MEDIA QUERIES                               */
/* -------------------------------------------------------------------------- */
@media screen and (max-width: 800px) {
  main {
    height: 90%;
    grid-template-columns: 300px;
    grid-template-rows: 450px 450px 450px;
    overflow: visible;
    margin-top: 50px;
  }

  .sedan-sec {
    border-radius: 10px 10px 0 0;
  }

  .luxury-sec {
    border-radius: 0 0 10px 10px;
  }
}
@media screen and (min-width: 800px) {
  .footer {
    visibility: hidden;
  }
}